<template>
  <div v-if="account">
    <b-container class="pt-5">
      <b-row class="justify-content-md-center pb-3">
        <b-col md="6">
          <router-link to="/admin/cuentas">
            <b-icon-arrow-left /> Ir al listado
          </router-link>
        </b-col>
      </b-row>
      <b-row class="justify-content-md-center">
        <b-col md="6">
          <b-form @submit.prevent="onSubmit">
            <b-row>
              <b-col md="12">
                <b-form-group>
                  <label>Cuenta:</label>
                  <b-form-input v-model="account.name"></b-form-input>
                </b-form-group>
              </b-col>
              <b-col>
                <b-form-group>
                  <label>Email:</label>
                  <b-form-input v-model="account.email"></b-form-input>
                </b-form-group>
              </b-col>
              <b-col>
                <b-form-group>
                  <label>Teléfono:</label>
                  <b-form-input v-model="account.phone"></b-form-input>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col>
                <label>Estado:</label>
                <b-form-select v-model="account.state" @change="getCities">
                  <b-form-select-option v-for="state in catalogs.states" :key="state.uuid" :value="state.id">
                    {{state.name}}
                  </b-form-select-option>
                </b-form-select>
              </b-col>
              <b-col>
                <label>Ciudad:</label>
                <b-form-select v-model="account.city">
                  <b-form-select-option v-for="city in cities" :key="city.uuid" :value="city.id">
                    {{city.name}}
                  </b-form-select-option>
                </b-form-select>
              </b-col>
            </b-row>
            <b-row class="pt-3 pb-3">
              <b-col>
                <b-button disabled type="submit">Guardar</b-button>
              </b-col>
            </b-row>
          </b-form>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>

import { getAccount, updateAccount } from '@/services/accounts'
import { getCities } from '@/services/geo'
import { mapActions, mapState } from 'vuex'

export default {
  data: () => ({
    account: null,
    cities: [],
  }),
  computed: {
    ...mapState(['auth', 'catalogs'])
  },
  methods: {
    ...mapActions({
      'getStates': 'catalogs/getStates',
      'getPositions': 'catalogs/getPositions',
      'createComplaint': 'complaints/create'
    }),
    async getAccount(id) {
      const { data: response } = await getAccount(id);
      return response.data
    },
    async getCities(state) {
      const {data: result} = await getCities(state);
      this.cities = result.data;
    },
    // async onSubmit() {
    //   const id = this.$route.params.id
    //   const {data: response} = await updateAccount(id, this.account)
    // }
  },
  async mounted() {
    const id = this.$route.params.id
    try {
      this.account = await this.getAccount(id)
      await this.getStates()
      await this.getCities(this.account.state)
      await this.getPositions()
    } catch (err) {
      this.$snack.danger({
        text: err.message
      })
    }
  }
}
</script>
