<template>
  <modal name="newAccount" class="modal--login" :adaptive="true" :scrollable="true" height="auto">
    <div class="row">
      <div class="col">
        <div class="p-3">
          <h3>Crear cuenta</h3>
          <b-form @submit.prevent="submit">
            <b-form-group>
              <label>Nombre de la cuenta</label>
              <b-form-input v-model="form.account"></b-form-input>
            </b-form-group>
            <b-form-group>
              <label>Nombre del usuario administrador</label>
              <b-form-input v-model="form.name"></b-form-input>
            </b-form-group>
            <b-form-group>
              <label>Email del administrador:</label>
              <b-form-input v-model="form.email"></b-form-input>
            </b-form-group>
            <b-form-group>
              <label>Teléfono del administrador:</label>
              <b-form-input v-model="form.phone"></b-form-input>
            </b-form-group>
            <b-form-group>
              <label>Contraseña:</label>
              <b-form-input v-model="form.password"></b-form-input>
            </b-form-group>
            <b-row>
              <b-col>
                <b-form-group>
                  <label>Estado:</label>
                  <b-form-select v-model="form.state" @change="getCities">
                    <b-form-select-option v-for="state in catalogs.states" :key="state.uuid" :value="state.id">
                      {{state.name}}
                    </b-form-select-option>
                  </b-form-select>
                </b-form-group>
              </b-col>
              <b-col>
              <b-form-group>
                <label>Ciudad:</label>
                <b-form-select v-model="form.city">
                  <b-form-select-option v-for="city in cities" :key="city.uuid" :value="city.id">
                    {{city.name}}
                  </b-form-select-option>
                </b-form-select>
              </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col md="6" offset-md="6" style="text-align:right">
                <b-button type="submit">Guardar</b-button>
              </b-col>
            </b-row>
          </b-form>
        </div>
      </div>
    </div>
  </modal>
</template>

<script>

import { getCities } from '@/services/geo'
import { createAccount } from '@/services/accounts'
import { mapState } from 'vuex';

const initialFormState = {
    name: null,
    email: null,
    phone: '+52',
    account: null,
    city: null,
    state: null,
    password: null
}

export default {
  data: () => ({
    form: initialFormState,
    cities: []
  }),
  computed: {
    ...mapState(['auth', 'catalogs'])
  },
  methods: {
    async getCities(state) {
      const {data: result} = await getCities(state);
      this.cities = result.data;
    },
    async submit(e) {
      try {
        const { data: response } = await createAccount({
          name: this.form.name,
          email: this.form.email,
          phone: this.form.phone,
          account: this.form.account,
          city: this.form.city,
          state: this.form.state,
          password: this.form.password
        })
        e.target.reset()
        this.$emit('newAccount', response.data)
      } catch (err) {
        this.$emit('error', err)
      }
    }
  }
}
</script>
