<template>
  <div class="page">
    <AddAccound @newAccount="newAccount" @error="onError" />
    <b-container>
      <b-row class="justify-content-md-center">
        <b-col md="9">
          <div class="p-3">
            <h1>Usuarios</h1>
            <b-pagination
              :total-rows="total"
              :per-page="limit"
              first-text="Primera"
              prev-text="Ant."
              next-text="Sig."
              last-text="Última"
              align="right"
              v-model="page"
              @change="changePage"
            ></b-pagination>
            <b-table :busy="fetching" responsive striped hover :items="rows" :fields="fields" :per-page="limit" @row-dblclicked="onClickRow" caption-top>
              <template #table-caption>
                <b-button @click="openNewAccountModal">
                  Agregar cuenta
                </b-button>
              </template>
              <template #cell(acciones)="data">
                <!-- <b-button size="sm" @click.prevent="edit(data.index)" class="mr-1" v-if="(auth.user.role.code === 'ADMIN' && !fetching) && data.item.nombre !== 'Root'">
                  <b-icon-pencil-square />
                </b-button> -->
                <b-button v-if="(auth.user.role.code === 'ADMIN' && !fetching) && data.item.nombre !== 'Root'" size="sm" variant="danger" @click.prevent="deleteItem(data.index)">
                  <b-icon-trash />
                </b-button>
              </template>
            </b-table>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>

import { getUsers } from '@/services/users'
import { getCities } from '@/services/geo'
import { mapState, mapActions } from 'vuex'
import AddAccound from './AddAccount.vue'

export default {
  components:{
    AddAccound
  },
  data: ()=> ({
    accounts: [],
    fields: [],
    rows: [],
    pages: 1,
    page: 1,
    total: 0,
    limit: 10,
    fetching: false
  }),
  computed:{
    ...mapState(['auth', 'catalogs'])
  },
  methods: {
    ...mapActions({
      'getStates': 'catalogs/getStates',
    }),
    async getAccounts(filter){
      const {data: response} = await getAccounts(filter)
      this.page = response.data.page
      this.pages = response.data.pages
      this.limit = response.data.limit
      this.total = response.data.total
      return response.data.docs
    },
    async changePage(page){
      await this.reloadAccounts(page)
    },
    onClickRow(record, index){},
    edit(index){
      const id = this.accounts[index].id
      this.$router.push(`/admin/cuentas/${id}`)
    },
    openNewAccountModal() {
      this.$modal.show('newAccount')
    },
    async newAccount() {
      this.accounts = this.reloadAccounts(1)
      this.$modal.hide('newAccount')
    },
    async reloadAccounts(page) {
      this.fetching = true
      this.accounts = await this.getAccounts({ page, populate: 'city,state', limit: this.limit })
      this.rows = this.accounts.map((account, index) => {
        const city = (account.city || {}).name || ''
        const state = (account.state || {}).name || ''
        return {
          '#': index + 1,
          nombre: account.name,
          ciudad: city,
          ciudad: state,
          acciones: ''
        }
      })
      this.fetching = false
    },
    async onError(error) {
      this.$snack.danger({
        text: error.response.data.message
      })
    }
  },
  async mounted() {
    if (!this.auth.accessToken) {
      this.$router.push('/')
    }
    await this.reloadAccounts(1)
    await this.getStates()
    this.fields = [
        {key: '#', sortable: true},
        {key: 'nombre', sortable: true},
        {key: 'ciudad', sortable: true},
        {key: 'estado', sortable: true},
        {key: 'acciones', sortable: false},
    ]
  }
}
</script>
