import axios from "./axios";
import qs from "querystring";

export function getAccounts(filter = {}) {
  return axios.get(`/accounts?${qs.encode(filter)}`);
}

export function getAccount(id, filter = {}) {
  return axios.get(`/accounts/${id}?${qs.encode(filter)}`);
}

export function updateAccount(id, data) {
  return axios.patch(`/accounts/${id}`);
}

export function createAccount({
  account,
  name,
  state,
  city,
  email,
  phone,
  password
}) {
  return axios.post("/accounts", {
    account,
    name,
    state,
    city,
    email,
    phone,
    password
  });
}
